import React, { FC } from "react";
import styled from "styled-components";

import { Icon } from "../Icon/Icon";
import { spacingMap } from "../styleguide";

import { BadgeVariantIconColors, StyledBadge, StyledBadgeText } from "./Badge.styles";
import { BadgeProps, StyledBadgeProps } from "./Badge.types";

const BadgeIconWrapper = styled.span<Pick<StyledBadgeProps, "size">>`
	margin: ${({ size }) => size === "small" ? `6px 0 6px ${spacingMap.xxs}` : `6px 0 6px ${spacingMap.xs}`};
	display: flex;
	vertical-align: middle;
`;

const Badge: FC<BadgeProps> = ({
	dataQA,
	text,
	variant = "default",
	icon,
	size = "default",
	className,
	m,
	mt,
	mb,
	mr,
	ml,
	isIconFilled = true,
}) => (
	<StyledBadge
		data-qa={dataQA}
		variant={variant}
		size={size}
		className={className}
		m={m}
		mt={mt}
		mb={mb}
		mr={mr}
		ml={ml}
	>
		{icon && (
			<BadgeIconWrapper size={size}>
				<Icon
					role="presentation"
					color={BadgeVariantIconColors[variant]}
					fill={isIconFilled ?
						BadgeVariantIconColors[variant] :
						"none"}
					name={icon}
					size={size === "small" ? 8 : 12}
				>
				</Icon>
			</BadgeIconWrapper>
		)}
		{text && (
			<StyledBadgeText
				dataQA="badge-text"
				variant={size === "small" ? "caption" : "bodyCopySmall"}
				component="span"
				size={size}
			>
				{text}
			</StyledBadgeText>
		)}
	</StyledBadge>
);

Badge.displayName = "Badge";
export default Badge;
